import { Component } from '@angular/core';
import { ViewTemplateInterface } from '../../../utils/components/th2-nova-view-template-component/models/th2-nova-view-template.model';

@Component({
    selector: 'demo-template-action-component',
    templateUrl: 'templateAction-view.component.html'
})
export class TemplateActionViewComponent {
    public viewTemplateConfig: ViewTemplateInterface = {
        title: '',
        actions: [
            {
                mode: 'link',
                label: 'Eliminar',
                icon: 'trash'
            },
            {
                mode: 'button',
                label: 'Añadir documentación',
                icon: 'home',
                routerlink: '/product',
                id: 'delete21'
            }
        ]
    };

    public doClick(id: any) {
        switch (id) {
            case 'delete20':
                {
                    alert('Delete');
                }
                break;
        }
    }
}
