import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { TemplateActionViewComponent } from './templateAction-view.component';

const routes: Routes = [{ path: '', component: TemplateActionViewComponent }];

@NgModule({
    exports: [RouterModule],
    imports: [RouterModule.forChild(routes)]
})
export class TemplateActionViewRoutingmodule {}
